import returns from '../common/returns.vue'
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		returns,
		uniLoadMore
	},
	data() {
		return {
			show: 0,
			data: [],
			status: '',
			more: 'more',
			page: 1,
			loadRecord: true
		}
	},
	methods: {
		returns() {
			uni.switchTab({
				url: '/pages/index/user'
			});
		},
		jump(url) {
			
			uni.navigateTo({
				url: url
			})
		},
		loadMore() {
			let page = this.page
			this.more = 'loading'
			this.service.entire(this, 'get', this.APIconfig.api_root.subuser.g_AftersaleLists, {
				user_id:this.$store.state.user.id,
				is_more: 1,
				status: this.show,
				page: page
			}, function(self, res) {
				
				let data = self.data

				data.push(...res.data.data)
				self.data = data
				console.log(self.data);
				self.page = page + 1
				self.more = 'more'
				if (res.data.data.length < 10) {
					self.more = 'noMore'
					self.loadRecord = false
					return
				}
			})
		}
	},
	onLoad(options) {
		this.status = options.status
		this.show = options.status
	},
	onReachBottom() {
		if (this.loadRecord == false) return
		this.loadMore()
	},
	onShow() {
		this.data.length = 0
		this.page = 1
		this.loadMore()
	}
}
